import {request} from "./request";

export default {
    insertOneNotify(userId, sendId, msg, pl, time, nickName, imgUrl, state, type, id, videoImg) {
        let data;
        request({
            url:"/insertOneSysNotify",
            method: "get",
            params:{
                userId:userId,
                sendId: sendId,
                msg: msg,
                pl: pl,
                time:time,
                nickName:nickName,
                imgUrl:imgUrl,
                state:state,
                type: type,
                id:id,
                videoImg:videoImg
            }
        }).then(res=>{
            data = res.data;
            console.log(res.data);

        }).catch(err=>{
            console.log(err);
        });
        return data;
    },
    addOneView(id){
        return request({
            url:"/addOneView",
            method: "post",
            params:{
                id:id
            }
        })
    }
}