                <el-input
                        type="textarea"
                        :rows="3"
                        placeholder="请输入内容"
                        v-model="textarea"
                        style="width: 700px;"

                >
                </el-input>

                <el-button type="primary" class="subBtn"  @click="submitComment" >提交评论</el-button>
              <el-button type="success" style="margin-top:10px " @click="qiandao">签到</el-button>
            </div>
            <div>
                <ul class="pl-type">
                    <li class="pl-active">按热度排序</li>
