</template>

<script>
    import videoPlayItem from "../../components/common/video";
    import {request} from "../../network/request";
    import constant from "../../utils/constant";
    import netRequest from "../../network/netRequest";
    import {getCourseInfo,educomment,listByCourseId,courseitemsigned,getStatus} from '@/api/user';
    export default {
        name: "video-play",
        data() {
            return {
                danmu:'开启弹幕',
                value2:'',
                textarea: '',
                url:'',
                id:this.$route.query.id,
                name:this.$route.query.name,
                collection:parseInt(this.$route.query.collection),
                views:'',
                size:this.$route.query.size,
                time:this.$route.query.time,
                upId:this.$route.query.courseId,
                upName:this.$route.query.upName,
                des:this.$route.query.des,
                point:parseInt(this.$route.query.point),
                pl:50,
                isPoint:0,
                isCollection:0,
                myHeadImg:sessionStorage.getItem("imgUrl"),
                comments:[],
                SendPoint:true,
                sendCollection:true,
                userId:sessionStorage.getItem("userId"),
                remen:[],
                video:[],
                listnames:[],
              total:'',
              courseId: ''
            }
        },

        components:{
            videoPlayItem,
        },
        created() {
          this.listByCourseId();
          getCourseInfo(this.id).then(res => {
            this.url = res.data.data.chapterList[0].videoUrl
            this.listnames = res.data.data.chapterList
            })
            request({
                url:"/addOneView",
                method: "post",
                params:{
                    id:parseInt(this.$route.query.id)
                }
            }).then(res=>{
                this.views = res.data.views;
                this.point = res.data.point;
                this.collection = res.data.collection;
            }).catch(err=>{
                console.log(err);
            });
            request({
                url:"/getVideoList",
                method: "get",
                params:{
                }
            }).then(res=>{
                //console.log(res.data);
                this.remen = res.data;
            }).catch(err=>{
                console.log(err);
            });


            console.log(this.$route.query.id);
            // request({
            //     url:"/getAllComment",
            //     method: "post",
            //     params:{
            //         videoId:parseInt(this.$route.query.id)
            //     }
            // }).then(res=>{
            //     this.comment = res.data;
            //     this.comment.reverse();
            //     for (let i = 0; i < this.comment.length; i++){
            //         this.getImgHead(i, this.comment[i].userId);
            //     }
            // }).catch(err=>{
            //     console.log(err);
            // });

        },
      methods:{
          qiandao(){
            var datas={
              courseId:this.id,
              userId:sessionStorage.getItem('id')
            }
            courseitemsigned(datas).then(res => {
              if (res.data.code ===0 ){
                this.$message.success("签到成功")
                sessionStorage.setItem('code','0')
              }
            });
          },
          a(){
            console.log(this.url)
          },
          listByCourseId(){
            listByCourseId(this.id).then(res => {
              this.comments = res.data.data.records
              this.total = res.data.data.total
            })
          },
          changeVideo(i){
              this.url = this.listnames[i].videoUrl
          },
            changePage(id){
                let data = netRequest.addOneView(id).then(res => {
                    console.log(res.data);
                    let data = res.data;
                    this.$router.push({
                        path:"/videoPlay",
                        query:{
                            url:data.url,
                            name:data.name,
                            point:data.point,
                            views:data.views,
                            collection:data.collection,
                            id:data.id,
                            size:data.size,
                            upId:data.upId,
                            upName:data.upName,
                            des:data.des,
                            imgUrl:data.imgUrl,
                            time:data.time,
                        }
                    });
                    location.reload();
                });

            },
            // danmuchange(){
            //   if(this.value2 == true){
            //       this.danmu = '开启弹幕';
            //   }else{
            //       this.danmu = '关闭弹幕';
            //   }
            // },
            getImgHead(i, userId){
                request({
                    url:"/getImgUrl",
                    method: "post",
                    params:{
                        userId:userId
                    }
                }).then(res=>{
                    this.comment[i].imgUrl = res.data;
                    console.log(res.data)
                }).catch(err=>{
                    console.log(err);
                });
            },
            addPoint(){
                if(this.userId == null){
                    this.$router.replace("/login");
                }
                if(this.isPoint == 0){
                    this.point += 1;
                    document.getElementById("point").style.color = "#00b0ff";
                }
                else{
                    document.getElementById("point").style.color = "black";
                    this.point -= 1;
                }
                this.isPoint ^= 1;
                request({
                    url:"/addOnePoint",
                    method: "post",
                    params:{
                        id:parseInt(this.$route.query.id)
                    }
                }).then(res=>{

                   // console.log(res.data);

                }).catch(err=>{
                    console.log(err);
                });

                if (this.SendPoint){
                    this.SendPoint = false;
                    let userId = sessionStorage.getItem("userId");
                    let videoId = parseInt(this.id);
                    let nickName = sessionStorage.getItem("nickName");
                    let time = new Date().getFullYear() + "年" + (new Date().getMonth() + 1) + "月" + new Date().getDate() + "日";
                    console.log(this.$route.query.imgUrl);
                    //给up猪发一个通知消息
                    request({
                        url:"/insertOneSysNotify",
                        method: "get",
                        params:{
                            userId:parseInt(this.upId),
                            sendId: parseInt(userId),
                            msg: "给我的视频点了一个赞",
                            pl: "",
                            time:time,
                            nickName:nickName,
                            imgUrl:sessionStorage.getItem("imgUrl"),
                            state:0,
                            type:"video",
                            id:this.id,
                            videoImg:this.$route.query.imgUrl
                        }
                    }).then(res=>{

                        console.log(res.data);

                    }).catch(err=>{
                        console.log(err);
                    });
                }
            },
            addCollection(){
                if(this.userId == null){
                    this.$router.replace("/login");
                }
                if(this.isCollection == 0){
                    this.collection += 1;
                    document.getElementById("collection").style.color = "#00b0ff";
                }
                else{
                    document.getElementById("collection").style.color = "black";
                    this.collection -= 1;
                }
                this.isCollection ^= 1;
                request({
                    url:"/addOneCollection",
                    method: "post",
                    params:{
                        userId:parseInt(sessionStorage.getItem("userId")),
                        id:parseInt(this.$route.query.id)
                    }
                }).then(res=>{

                    console.log(res.data);

                }).catch(err=>{
                    console.log(err);
                });

                if(this.sendCollection){
                    this.sendCollection = false;
                    let userId = sessionStorage.getItem("userId");
                    let videoId = parseInt(this.id);
                    let nickName = sessionStorage.getItem("nickName");
                    let time = new Date().getFullYear() + "年" + (new Date().getMonth() + 1) + "月" + new Date().getDate() + "日";
                    //给up猪发一个通知消息
                    request({
                        url:"/insertOneSysNotify",
                        method: "get",
                        params:{
                            userId:parseInt(this.upId),
                            sendId: parseInt(userId),
                            msg: "收藏了我的视频",
                            pl: "",
                            time:time,
                            nickName:nickName,
                            imgUrl:sessionStorage.getItem("imgUrl"),
                            state:0,
                            type:"video",
                            id:this.id,
                            videoImg:this.$route.query.imgUrl
                        }
                    }).then(res=>{

                        console.log(res.data);

                    }).catch(err=>{
                        console.log(err);
                    });
                }
            },
            submitComment(){
                if(sessionStorage.getItem('username')== null){
                    this.$router.replace("/login");
                }
                var userID= sessionStorage.getItem('id')
              var data={
                commentContent:this.textarea,
                userId:userID,
                courseId:this.id
              }
              educomment(data).then((response)=>{
                if (response.data.code === 0){
                  debugger
                  this.listByCourseId();
                  this.$message.success("评论成功")
                }
              })

                let userId = sessionStorage.getItem("userId");
                let videoId = parseInt(this.id);
                let nickName = sessionStorage.getItem("nickName");
                let time = new Date().getFullYear() + "年" + (new Date().getMonth() + 1) + "月" + new Date().getDate() + "日";
                // request({
                //     url:"/insertOne",
                //     method: "post",
                //     params:{
                //         msg:this.textarea,
                //         videoId: videoId,
                //         userId: userId,
                //         nickName: nickName,
                //         imgUrl:this.myHeadImg,
                //         time:time
                //     }
                // }).then(res=>{
                //
                //     this.$message({
                //         message: '发布成功',
                //         type: 'success'
                //     });
                //     //console.log(res.data);
                //
                //
                // }).catch(err=>{
                //     console.log(err);
                // });
                let t = {
                    msg:this.textarea,
                    videoId: videoId,
                    userId: userId,
                    nickName: nickName,
                    imgUrl:this.myHeadImg,
                    time:time
                }

                //console.log(t);
                this.comment.unshift(t);


                //给up猪发一个通知消息
                request({
                    url:"/insertOneSysNotify",
                    method: "get",
                    params:{
                        userId:parseInt(this.upId),
                        sendId: parseInt(userId),
                        msg: "评论了我的视频",
                        pl: this.textarea,
                        time:time,
                        nickName:nickName,
                        imgUrl:sessionStorage.getItem("imgUrl"),
                        state:0,
                        type:"video",
                        id:this.id,
                        videoImg:this.$route.query.imgUrl
                    }
                }).then(res=>{

                    console.log(res.data);

                }).catch(err=>{
                    console.log(err);
                });
                this.textarea = '';
            }
        },



    }
</script>

<style scoped>
    .remeng-tuijian .bo{
        display: inline-block;
        height: 16px;
        color: #999;
        font-size: 14px;
        display: inline-block;
        margin-left: 10px;
        position: absolute;
        width: 200px;
        cursor: pointer;
        left: 140px;
        top: 60px;
    }
    .remeng-tuijian .up{
        cursor: pointer;
        display: inline-block;
        height: 16px;
        color: #999;
        font-size: 14px;
        margin-left: 10px;
        top: -30px;
        width: 200px;
        position: relative;
    }
    .remeng-tuijian .title{
        cursor: pointer;
        width: 200px;
        display: inline-block;
        font-size: 14px;
